body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 10s linear;
          animation: App-logo-spin infinite 10s linear;
  height: 7vmin;
  margin-top: 20px;
  position: relative;
  top: -17px;
  right: 10px;
}

.App-header {
  background-color: #282c34;
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-header h1 {
  display: inline-block;
}

.App-link {
  color: #61dafb;
}

a:hover {
  text-decoration: none;
}

h1 {
  font-size: 3.5rem;
  padding: 15px 0;
}

h3 {
  font-size: 1.85rem;
}

p {
  font-size: 1.4rem;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

footer {
  background-color: #282c34;
  padding: 20px 0;
  margin-top: 20px;
}

footer small {
  color: #fff;
}

.clearfix::after {
  content: "";
  clear: both;
  display: table;
}

nav {
   display: flex;
   flex-direction: row;
   justify-content: space-between;
}
nav a {
    border: 1px solid #434343;
    padding: 5px 10px;
    font-size: 18px;
}
nav input[type="text"] {
    padding: 0 10px;
    color: #000;
    font-size: 18px;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
}
nav a:first-child {
    border-radius: 0 0 0 5px;
}

header a, header a:visited, header a:hover, header a:active, header a:focus {
    text-decoration: none;
    color: #fff;
}
.movieImg {
    max-height: 260px;
}

.movieBox {
    border: 3px solid #f0f0f0;
    border-radius: 5px;
    margin-bottom: 10px;
    padding-bottom: 15px;
}
.movieBox:hover {
    border: 3px solid #dedede;
}


.moviesHolder h3{
    padding: 20px 0;
}

.moviesHolder img{
    margin: 0 auto;
}

main input[type="text"].search-movie {
    width: 50%;
    padding: 3px 10px;
    font-size: 14px;
}
#sortMoviesBtn {
    margin-bottom: 10px;
    border: 3px solid #ccc;
    border-radius: 5px;
    background-color: #282c34;
    color: #fff;
    padding: 3px 15px;
    cursor: pointer;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}
#sortMoviesBtn:hover {
    background-color: #3d4450;
}
#sortMoviesBtn:focus {
    outline: none;
}
.sortMovies ul {
    display: table;
    margin: 0 auto;
    padding: 0;
}

.sortMovies ul li {
    list-style: none;
    float: left;
    margin-right: 15px;
    border: 1px solid #ccc;
    border-radius: 5px;
    background-color: #282c34;
    color: #fff;
    padding: 3px 15px;
    margin-bottom: 10px;
    cursor: pointer;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}
.sortMovies ul li:hover {
    background-color: #3d4450;
}

.sortMovies ul li:last-of-type {
    margin-right: 0;
}

.sortMovies ul li span{
    font-size: 16px;
}

  
  .moviesHolder a {
    text-decoration: none;
    color: #000;
  }
  
  .moviesHolder h3 {
    margin-bottom: 0;
  }
  
  .image-holder {
    margin: 0 auto;
    display: table;
    position: relative;
  }
  
  .image-holder img {
    float: right;
  }
  
  .delete-btn {
    padding: 5px 13px;
    border: 1px solid #ccc;
    background-color: #fff;
    color: #000;
    font-weight: normal;
    border-radius: 2px;
    font-size: 12px;
    float: right;
    position: absolute;
    right: 0;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    cursor: pointer;
  }
  
  .delete-btn:hover {
    background-color: #f3f3f3;
  }

  @media only screen and (max-width: 576px) {
    .movieBox, .movieBox:hover {
        border: none;
    }

    #sortMoviesBtn {
        font-size: 14px;
    }
     
}
form {
    padding: 20px;
}
form label {
    display: block;
}

form input {
    padding: 5px;
}
form input[type="text"] {
    margin-bottom: 10px;
    width: 50%;
    border-radius: 5px;
    border: 1px solid gray
}

form input[type="text"]:last-child {
    margin-bottom: 20px;
}

input[type="submit"] {
    display: block;
    margin: 0 auto;
    width: 30%;
    background-color: #3fcf6b;
    border: none;
    padding: 5px;
    border-radius: 5px;
    color: #fff;
    font-size: 18px;
}

form input[type="submit"]:hover {
    background-color: #71ff9d;
}

.imagePlaceholder {
    height: 300px;
    max-width: 300px;
    margin: 0 auto;
    border-radius: 8px;
    border: 2px solid gray;
    margin-bottom: 10px;
}
.imagePlaceholder img {
    max-height: 300px;
}
